import "./styles.css";

import {
  WebGLRenderer,
  PerspectiveCamera,
  Scene,
  CubicBezierCurve3,
  Vector3,
  BufferGeometry,
  LineBasicMaterial,
  Line,
  AmbientLight,
  Color,
  EllipseCurve,
  MeshBasicMaterial,
  Mesh,
  DoubleSide,
  EdgesGeometry,
  LineSegments,
  WireframeGeometry
} from "three";

import { ParametricGeometry } from "three/examples/jsm/geometries/ParametricGeometry";

import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";

const container = document.querySelector("#app");

const renderer = new WebGLRenderer();
renderer.setSize(container.clientWidth, container.clientHeight);
// renderer.setClearColor(0x000000, 0);

const camera = new PerspectiveCamera(
  45,
  container.clientWidth / container.clientHeight,
  1,
  2000
);
const controls = new OrbitControls(camera, renderer.domElement);
const scene = new Scene();

scene.background = new Color(0x404040);

const light = new AmbientLight(0x404040, 1); // soft white light
scene.add(light);

camera.position.set(0, 0, 50);
controls.target.set(0, 0, 0);

container.appendChild(renderer.domElement);

function animate() {
  requestAnimationFrame(animate);

  // required if controls.enableDamping or controls.autoRotate are set to true
  controls.update();

  renderer.render(scene, camera);
}

animate();

const { tan, cos, sin, PI } = Math;
const R = 10;
const phi = PI / 2;

const rot = PI;

{
  const curve = new CubicBezierCurve3(
    new Vector3(R, 0, 0).applyAxisAngle(new Vector3(0, 0, 1), rot),
    new Vector3(R, ((R * 4) / 3) * tan(phi / 4), 0).applyAxisAngle(
      new Vector3(0, 0, 1),
      rot
    ),
    new Vector3(
      R * (cos(phi) + (4 / 3) * tan(phi / 4) * sin(phi)),
      R * (sin(phi) - (4 / 3) * tan(phi / 4) * cos(phi)),
      0
    ).applyAxisAngle(new Vector3(0, 0, 1), rot),
    new Vector3(R * cos(phi), R * sin(phi), 0).applyAxisAngle(
      new Vector3(0, 0, 1),
      rot
    )
  );

  const points = curve.getPoints(50);
  const geometry = new BufferGeometry().setFromPoints(points);

  const material = new LineBasicMaterial({ color: 0xff0000 });

  // Create the final object to add to the scene
  const curveObject = new Line(geometry, material);
  scene.add(curveObject);

  const ellipse = new EllipseCurve(0, 10, R, R, 0, PI, false);
  const pts = ellipse.getPoints(50);
  const eg = new BufferGeometry().setFromPoints(pts);

  const ematerial = new LineBasicMaterial({
    color: 0x00ff00,
    transparent: true,
    opacity: 0.5
  });

  // Create the final object to add to the scene
  const eObject = new Line(eg, ematerial);
  scene.add(eObject);

  // const pg = new ParametricGeometry(plane(10, 10));

  const pg = new ParametricGeometry(
    (u, v, tar) => {
      const lerp = new Vector3().lerpVectors(
        curve.getPoint(u),
        ellipse.getPoint(1 - u),
        v
      );
      const { x } = lerp;
      const { y } = lerp;
      const z = Math.sin(Math.PI * v) * 5;
      tar.set(x, y, z);
    },
    5,
    7
  );

  const wireframe = new WireframeGeometry(pg);
  const line = new LineSegments(
    wireframe,
    new LineBasicMaterial({ color: 0xffffff })
  );
  scene.add(line);
  scene.add(
    new Mesh(
      pg,
      new MeshBasicMaterial({
        color: 0xff00ff,
        side: DoubleSide,
        transparent: true,
        opacity: 0.2
      })
    )
  );
}
